import styled from "styled-components";

const Input = styled.input`
  font-family: Helvetica, sans-serif;
  box-sizing: border-box;
  border-radius: 2px;
  outline: 0;
  border: 1px solid #888;
  height: 2em;
  padding: 0 0.5em;
  width: 100%;
`;

export default Input;



